<template>
    <video  @click="$say('clicked:IMAGE')" :class="[ 'IMAGE-BASIC', 'ROOT', params.level,params.wrap]" :src="data.url" 
        :controls="data.showControls||params.showControls"
        :autoplay="data.autoplay||params.autoplay"
        :data-video-size="data.size||params.size||'contain'"
        autofoc
    />
</template>
<style lang="less">
video.VIDEO-BASIC
{
    
    width: auto;
    height: auto;
    &[data-video-size="contain"]
    {
        object-fit: contain;
    }
    &[data-video-size="cover"]
    {
        object-fit: cover;
    }
}
</style>

<script>
/**
 * params={
 *      level:[h1,h2,h3,h4,h5,h6],
 *      wrap:[wrap,breakword]
 * }
 * data.IMAGE=String
 */
import v from '../validators.js';
var {vURL,vNumber,vString,vPromise,vObject,vRequired,$OR,$AND}=v;
export default {
    name:"VIDEO-BASIC",
    props:['data'],
    fields:{
        data:{
            is:/^IMAGE.*/,
            url:vURL
        }
    }
}
</script>



